a {
  text-decoration: none;
}
p, span, a {
  font-family: "roboto", sans-serif;
}
.container {
  &.wrap {
    padding-left: 35px;
    padding-right: 35px;

    @media (max-width: $screen-xs-max) {
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

.breadcrumbs {
  background-color: #fafafa;
  padding: 17px 0 16px;

  @media (max-width: $screen-xs-max) {
    display: none;
  }

  &__link {
    position: relative;
    font-family: "roboto", sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: #000;
    margin-right: 22px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    &:hover {
      color: #666666;
    }

    &:after {
      content: '';
      position: absolute;
      pointer-events: none;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 3.5px 0 3.5px 4px;
      border-color: transparent transparent transparent #000000;
      right: -14px;
      top: 5px;
    }

    &:last-child {
      color: #666666;
      pointer-events: none;
      margin-right: 0;

      &:after {
        display: none;
      }
    }

    &:first-child {
      margin-left: 24px;
    }
  }
}

.paragraph {
  &:first-letter {
    margin-left: 38px;
  }
}
label.error {
  font-family: "roboto", sans-serif;
  display: inline-block;
  font-size: 16px;
  margin-left: 15px;
  color: #e2001a;

  @media (max-width: $screen-xs-max) {
    margin: 5px 0 0 0;
  }
}

.page-pagination {
  margin-bottom: 18px;

  &__wrapper {
    text-align: center;
    padding-left: 28px;

    @media (max-width: $screen-xs-max) {
      padding-left: 0;
    }

    a {
      display: inline-block;
      font-family: "roboto", sans-serif;
      font-size: 22px;
      font-weight: 700;
      color: #4c4c4c;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;

      &:hover {
        color: #666;
      }

      &:not(:last-child) {
        margin-right: 28px;
      }

      &.hidden {
        visibility: hidden;
        opacity: 0;
      }

      &.active {
        color: #e2001a;
        pointer-events: none;
      }

      &.other {
        position: relative;
        top: -6px;
        pointer-events: none;
      }
    }

    &--white {
      a {
        color: #fff;

        svg {
          fill: #fff;
        }
      }
    }
  }

  &__navigation {
    position: relative;
    width: 7px;
    height: 12px;

    &:hover {

      svg {
        fill: #666666;
      }
    }

    &--prev {
      transform: rotate(180deg);
    }

    svg {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      fill: #4c4c4c;
      -webkit-transition: all 0.2s ease;
      transition: all 0.2s ease;


    }
  }
}

.index {
  padding: 40px 0;
  display: flex;
  flex-direction: column;

  a {
    color: #e2001a;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    margin-bottom: 5px;

    &:hover {
      color: #ff2c2c;
    }
  }
}

.jq-checkbox,
.jq-radio {
  position: relative;
  display: inline-block;
  overflow: hidden;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.jq-checkbox input,
.jq-radio input {
  position: absolute;
  z-index: -1;
  margin: 0;
  padding: 0;
  opacity: 0;
}
.jq-file {
  position: relative;
  display: inline-block;
  overflow: hidden;
}
.jq-file input {
  position: absolute;
  top: 0;
  right: 0;
  height: auto;
  margin: 0;
  padding: 0;
  opacity: 0;
  font-size: 100px;
  line-height: 1em;
}
.jq-file__name {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.jq-selectbox,
.jq-select-multiple {
  position: relative;
  display: inline-block;
}
.jq-selectbox select,
.jq-select-multiple select {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 0;
  opacity: 0;
}
.jq-selectbox li,
.jq-select-multiple li {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  white-space: nowrap;
}
.jq-selectbox {
  z-index: 10;
}
.jq-selectbox__select {
  position: relative;
}
.jq-selectbox__select-text {
  overflow: hidden;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.jq-selectbox__dropdown {
  position: absolute;
}
.jq-selectbox__search input {
  -webkit-appearance: textfield;
}
.jq-selectbox__search input::-webkit-search-cancel-button,
.jq-selectbox__search input::-webkit-search-decoration {
  -webkit-appearance: none;
}
.jq-selectbox__dropdown ul {
  position: relative;
  overflow: auto;
  overflow-x: hidden;
  list-style: none;
  -webkit-overflow-scrolling: touch;
}
.jq-select-multiple ul {
  position: relative;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.jq-number {
  display: inline-block;
}
.jq-number__field input {
  -moz-appearance: textfield;
  text-align: left; /* для Opera Presto */
}
.jq-number__field input::-webkit-inner-spin-button,
.jq-number__field input::-webkit-outer-spin-button {
  margin: 0; /* в каких-то браузерах присутствует отступ */
  -webkit-appearance: none;
}


.jq-checkbox,
.jq-radio {
  vertical-align: -4px;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
  border: 1px solid #C3C3C3;
  background: linear-gradient(#FFF, #E6E6E6);
  box-shadow: 0 1px 1px rgba(0,0,0,.05), inset -1px -1px #FFF, inset 1px -1px #FFF;
  cursor: pointer;
}
.jq-checkbox.focused,
.jq-radio.focused {
  border: 1px solid #08C;
}
.jq-checkbox.disabled,
.jq-radio.disabled {
  opacity: .55;
}
.jq-checkbox {
  border-radius: 3px;
}
.jq-checkbox.checked .jq-checkbox__div {
  width: 12px;
  height: 12px;
  margin: 2px 0 0 2px;
  border-radius: 2px;
  background: #666;
  box-shadow: inset 0 -3px 6px #AAA;
}
.jq-radio {
  border-radius: 50%;
}
.jq-radio.checked .jq-radio__div {
  width: 10px;
  height: 10px;
  margin: 3px 0 0 3px;
  border-radius: 50%;
  background: #777;
  box-shadow: inset 1px 1px 1px rgba(0,0,0,.7);
}
.jq-file {
  width: 270px;
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(0,0,0,.1);
}
.jq-file input {
  cursor: pointer;
}
.jq-file__name {
  box-sizing: border-box;
  width: 100%;
  height: 34px;
  padding: 0 80px 0 10px;
  border: 1px solid #CCC;
  border-bottom-color: #B3B3B3;
  border-radius: 4px;
  background: #FFF;
  box-shadow: inset 1px 1px #F1F1F1;
  font: 14px/32px Arial, sans-serif;
  color: #333;
}
.jq-file__browse {
  position: absolute;
  top: 1px;
  right: 1px;
  padding: 0 10px;
  border-left: 1px solid #CCC;
  border-radius: 0 4px 4px 0;
  background: linear-gradient(#FFF, #E6E6E6);
  box-shadow: inset 1px -1px #F1F1F1, inset -1px 0 #F1F1F1, 0 1px 2px rgba(0,0,0,.1);
  font: 14px/32px Arial, sans-serif;
  color: #333;
  text-shadow: 1px 1px #FFF;
}
.jq-file:hover .jq-file__browse {
  background: linear-gradient(#F6F6F6, #E6E6E6);
}
.jq-file:active .jq-file__browse {
  background: #F5F5F5;
  box-shadow: inset 1px 1px 3px #DDD;
}
.jq-file.focused .jq-file__name {
  border: 1px solid #5794BF;
}
.jq-file.disabled,
.jq-file.disabled .jq-file__name,
.jq-file.disabled .jq-file__browse {
  border-color: #CCC;
  background: #F5F5F5;
  box-shadow: none;
  color: #888;
}
.jq-number {
  position: relative;
  vertical-align: middle;
  padding: 0 36px 0 0;
}
.jq-number__field {
  width: 100px;
  border: 1px solid #CCC;
  border-bottom-color: #B3B3B3;
  border-radius: 4px;
  box-shadow: inset 1px 1px #F1F1F1, 0 1px 2px rgba(0,0,0,.1);
}
.jq-number__field:hover {
  border-color: #B3B3B3;
}
.jq-number__field input {
  box-sizing: border-box;
  width: 100%;
  padding: 8px 9px;
  border: none;
  outline: none;
  background: none;
  font: 14px Arial, sans-serif;
  color: #333;
}
.jq-number__spin {
  position: absolute;
  top: 0;
  right: 0;
  width: 32px;
  height: 14px;
  border: 1px solid #CCC;
  border-bottom-color: #B3B3B3;
  border-radius: 4px;
  background: linear-gradient(#FFF, #E6E6E6);
  box-shadow: 0 1px 2px rgba(0,0,0,.1);
  box-shadow: inset 1px -1px #F1F1F1, inset -1px 0 #F1F1F1, 0 1px 2px rgba(0,0,0,.1);
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-shadow: 1px 1px #FFF;
  cursor: pointer;
}
.jq-number__spin.minus {
  top: auto;
  bottom: 0;
}
.jq-number__spin:hover {
  background: linear-gradient(#F6F6F6, #E6E6E6);
}
.jq-number__spin:active {
  background: #F5F5F5;
  box-shadow: inset 1px 1px 3px #DDD;
}
.jq-number__spin:after {
  content: '';
  position: absolute;
  top: 4px;
  left: 11px;
  width: 0;
  height: 0;
  border-right: 5px solid transparent;
  border-bottom: 5px solid #999;
  border-left: 5px solid transparent;
}
.jq-number__spin.minus:after {
  top: 5px;
  border-top: 5px solid #999;
  border-right: 5px solid transparent;
  border-bottom: none;
  border-left: 5px solid transparent;
}
.jq-number__spin.minus:hover:after {
  border-top-color: #000;
}
.jq-number__spin.plus:hover:after {
  border-bottom-color: #000;
}
.jq-number.focused .jq-number__field {
  border: 1px solid #5794BF;
}
.jq-number.disabled .jq-number__field,
.jq-number.disabled .jq-number__spin {
  border-color: #CCC;
  background: #F5F5F5;
  box-shadow: none;
  color: #888;
}
.jq-number.disabled .jq-number__spin:after {
  border-bottom-color: #AAA;
}
.jq-number.disabled .jq-number__spin.minus:after {
  border-top-color: #AAA;
}
.jq-selectbox {
  vertical-align: middle;
  cursor: pointer;
}
@media (max-width: 543px) {
  .jq-selectbox {
    width: 100%;
  }
}
.jq-selectbox__select {
  border-radius: 4px;
  font-family: "roboto", sans-serif;
